import React, { Component } from 'react';
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { compose } from "redux";
import { connect } from "react-redux";
import { firestoreConnect } from "react-redux-firebase";
import Spinner from "../layout/Spinner.js";
var count = 0;

class EditClient extends Component {
    state = {};
    onChange = (e) => this.setState({ ...this.state, [e.target.name]: e.target.value });
    initState = (e) => this.setState({ e });
    // Update client
    onSubmit = (e) => {
        e.preventDefault();

        const updateClient = this.state;
        const { client, firestore, history } = this.props;
        if (client.balance === "") {
            updateClient.balance = 0;
        }

        firestore.update({ collection: "clients", doc: client.id }, updateClient).then(() =>
            history.push(`/client/${client.id}`));
    }

    render() {
        const { client } = this.props;
        const { disableBalanceOnEdit } = this.props.settings

        if (client) {
            if (count === 0) {
                this.initState(client)
                count++
            }
            return (
                <div>
                    <div className="row">
                        <div className="col-md-6">
                            <Link to={`/client/${client.id}`} className="btn btn-link">
                                <i className="fas fa-arrow-circle-left"></i> Back To Client Details
                        </Link>
                        </div>
                    </div>
                    <div className="card">
                        <div className="card-header">Edit Client</div>
                        <div className="card-body">
                            <form onSubmit={this.onSubmit}>
                                <div className="form-group">
                                    <label htmlFor="firstName">First Name</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="firstName"
                                        minLength="2"
                                        required
                                        onChange={this.onChange}
                                        autoFocus
                                        defaultValue={client.firstName}></input>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="lastName">Last Name</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="lastName"
                                        minLength="2"
                                        required
                                        onChange={this.onChange}
                                        defaultValue={client.lastName} />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="email">Email</label>
                                    <input
                                        type="type"
                                        className="form-control"
                                        name="email"
                                        minLength="2"
                                        required
                                        onChange={this.onChange}
                                        defaultValue={client.email} />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="phone">Phone</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="phone"
                                        minLength="10"
                                        onChange={this.onChange}
                                        defaultValue={client.phone} />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="balance">Balance</label>
                                    <input
                                        type="number"
                                        step="0.01"
                                        min="0"
                                        className="form-control"
                                        name="balance"
                                        onChange={this.onChange}
                                        defaultValue={client.balance}
                                        disabled={disableBalanceOnEdit}
                                    />
                                </div>

                                <input type="submit" value="Submit" className="btn btn-primary btn-block"></input>
                            </form>
                        </div>
                    </div>
                </div>
            );
        } else {
            return <Spinner />
        }
    }
}

EditClient.propTypes = {
    firestore: PropTypes.object.isRequired,
    settings: PropTypes.object.isRequired
}

export default compose(
    firestoreConnect(props => [
        { collection: "clients", storeAs: "client", doc: props.match.params.id }
    ]),
    connect(({ firestore: { ordered }, settings }, props) => ({
        client: ordered.client && ordered.client[0],
        settings
    }))
)(EditClient);