import { createStore, combineReducers, compose } from "redux";
import firebase from "firebase";
import "firebase/firestore";
import { reactReduxFirebase, firebaseReducer } from "react-redux-firebase";
import { reduxFirestore, firestoreReducer } from "redux-firestore";
// Reducers
import notifyReducer from "./reducers/notifyReducer.js"
import settingReducer from "./reducers/settingsReducer.js"

const firebaseConfig = {
    apiKey: "AIzaSyBrm-CX-Qe4netBCY4EzhvRiykCzvHwLQ0",
    authDomain: "client-panel-24102000.firebaseapp.com",
    databaseURL: "https://client-panel-24102000.firebaseio.com",
    projectId: "client-panel-24102000",
    storageBucket: "client-panel-24102000.appspot.com",
    messagingSenderId: "170720546841",
};

// react-redux-firebase config
const rrfConfig = {
    userProfile: 'users',
    useFirestoreForProfile: true //Firestore for Profile instead of Realtime DB
}

// Init firebase instance
firebase.initializeApp(firebaseConfig);
// Init firestore 
// const firestore = firebase.firestore();

// Add reactReduxFirebase enhancer when making store creator
const createStoreWithFirebase = compose(
    reactReduxFirebase(firebase, rrfConfig),
    reduxFirestore(firebase) // <- needed if using firestore
)(createStore);

// Add firebase to reducer
const rootReducer = combineReducers({
    firebase: firebaseReducer,
    firestore: firestoreReducer,
    notify: notifyReducer,
    settings: settingReducer
});

// Check for settings in local storage
if (localStorage.getItem('settings') === null) {
    // Default settings
    const defaultSettings = {
        disableBalanceOnAdd: true,
        disableBalanceOnEdit: false,
        allowRegistration: true
    }

    // Set to locastorage
    localStorage.setItem('settings', JSON.stringify(defaultSettings));
}

// Crate initial state
const initialState = {settings: JSON.parse(localStorage.getItem('settings'))};

// Create store with reducers and initial state
const store = createStoreWithFirebase(rootReducer, initialState, compose(
    reactReduxFirebase(firebase),
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__() : f => f
));

export default store;